import React, { Component } from 'react';
import { Form } from 'antd';
import DrawerRegisterForm from './DrawerRegisterForm';
import '../stylesheets/UserMessage.css';
import { Get } from '../utils/Request';

export default class UserMessage extends Component{
    constructor(props){
        super(props);
        this.state={
            user : {
                username : '张三'
            }
        };
    }
    componentDidMount(){
        let url = `/api/user/home/message?username=${this.state['user'].username}`;
        const res = Get(url);
        res.then(response=>{
            let {code, message, data} = response;
            
        });
    }
    render(){
        const WrappedRegisterForm = Form.create({ name: 'register' })(DrawerRegisterForm);
        return(<div id='userMessage'>
            <WrappedRegisterForm />
        </div>);
    }
}